.footer{
    width: 1200px;
}

.footer-color{
    background-color: #1E1823; 
}

.footer-space{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: baseline;
}

.footer-head{
    color: #fff;
    font-size: 28px;
    line-height: 34px;
    letter-spacing: -0.56px;
    font-weight: 500;
    width: 300px;
    padding-left: 32px;
    margin-top: 50px;
}

.footer-span{
    color: #FD9222;
}

.footer-column{
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
    padding-right: 63px;
    margin-top: 50px;
}

.footer-contact{
    color: #fff;
    text-align: center;
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    margin-bottom: 14px;
}

.footer-contact-ii{
    color: #fff;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: -0.28px;
    margin: 0px 0px 4px 0px;
}

.footer-column-ii{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-right: 63px;
}

.footer-svg{
    display: flex;
    align-items: center;
    gap: 18px;
}

.footer-svg-hover{
    stroke: #ffffff;
}

.footer-svg-hover:hover{
    stroke: #FD9222;
}

.footer-flex{
    display: inline-flex;
    align-items: flex-start;
    gap: 24px;
    padding: 0px 0px 50px 32px;

}

.footer-menu{
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: -0.28px;
}

.footer-svg-class{
    display: flex;
    align-items: center;
    fill:#FFFFFF;
    gap: 18px;
}

.footer-padding{
    padding-right: 543px;
    padding-bottom: 86px;
}

.footer-line{
    position: relative;
    display: inline-block;
    color: rgba(255, 255, 255, 0.10);
}

.footer-line::after{
    position: absolute;
    content: '';
    margin-left: 32px;
    width: 1136px;
    bottom: 90px;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.10);
}

